import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import { LineChart, Line, CartesianGrid, XAxis, YAxis, Tooltip } from 'recharts';

class App extends Component {

  constructor(props) {
    super(props);
    
    this.state = {
      activeYear:0
    };
      
    this.handleClick = this.handleClick.bind(this); 
    this.handleSubmit = this.handleSubmit.bind(this); 
}

  handleSubmit(e) {
      console.log("caught form submit...will prevent the event propagation now!")      
      e.preventDefault();    
  }
  handleClick(event) {
    console.log("caught handleClick..." + event.target.value)
    if(event.target.value == 2015) {
      this.setState({activeYear: 0});
    } else if(event.target.value == 2016) {
      this.setState({activeYear: 1});
    } else if(event.target.value == 2017) {
      this.setState({activeYear: 2});
    } else if(event.target.value == 2018) {
      this.setState({activeYear: 3});
    }
    console.log(this.state.activeYear);
  }
  
  render() {
    const yearEndData = [
      {year: '2014', amt: 98000},
      {year: '2015', amt: 77000},
      {year: '2016', amt: 41000},
      {year: '2017', amt: 38000},
      {year: '2018', amt: 60000},
      /*{year: '2019', amt: 120000},      
      {year: '2020', amt: 240000}, 
      {year: '2021', amt: 480000},
      {year: '2022', amt: 960000},
      {year: '2023', amt: 1920000},
      {year: '2024', amt: 3840000},
      {year: '2025', amt: 7680000}, */          
    ];
    const monthlyData = [
      {
        "year": 2015,
        "months": [
          {month: 'Jan', amt: 103000, percent: 5, percentAgainstContrib: 9},
          {month: 'Feb', amt: 104000, percent: 1, percentAgainstContrib: 10},
          {month: 'Mar', amt: 98000, percent: -6, percentAgainstContrib: 4},
          {month: 'Apr', amt: 93000, percent: -5, percentAgainstContrib: -2},
          {month: 'May', amt: 101000, percent: 9, percentAgainstContrib: 7},
          {month: 'Jun', amt: 87000, percent: -14, percentAgainstContrib: -8},
          {month: 'Jul', amt: 83000, percent: -5, percentAgainstContrib: -12},
          {month: 'Aug', amt: 81000, percent: -2, percentAgainstContrib: -14},
          {month: 'Sept', amt: 81000, percent: 0, percentAgainstContrib: -14},
          {month: 'Oct', amt: 79000, percent: -2, percentAgainstContrib: -16},
          {month: 'Nov', amt: 78000, percent: -1, percentAgainstContrib: -17},  
          {month: 'Dec', amt: 77000, percent: -1, percentAgainstContrib: -18}      
        ]
      },
      {
        "year": 2016,
        "months": [
          {month: 'Jan', amt: 59000, percent: -23, percentAgainstContrib: -38},
          {month: 'Feb', amt: 63000, percent: 7, percentAgainstContrib: -33},
          {month: 'Mar', amt: 63000, percent: 0, percentAgainstContrib: -33},
          {month: 'Apr', amt: 50000, percent: -21, percentAgainstContrib: -47},
          {month: 'May', amt: 53000 , percent: 6, percentAgainstContrib: -44},
          {month: 'Jun', amt: 52000, percent: -2, percentAgainstContrib: -45},
          {month: 'Jul', amt: 53000, percent: 2, percentAgainstContrib: -44},
          {month: 'Aug', amt: 51000, percent: 4, percentAgainstContrib: -46},
          {month: 'Sept', amt: 46000, percent: -10, percentAgainstContrib: -51},
          {month: 'Oct', amt: 35000, percent: -15, percentAgainstContrib: -63},
          {month: 'Nov', amt: 34000, percent: -2, percentAgainstContrib: -64},  
          {month: 'Dec', amt: 40000, percent: +18, percentAgainstContrib: -58}      
        ]
      },
      {
        "year": 2017,
        "months": [
          {month: 'Jan', amt: 40000, percent: 0, percentAgainstContrib: -58},
          {month: 'Feb', amt: 43000, percent: 8, percentAgainstContrib: -54},
          {month: 'Mar', amt: 44000, percent: 2, percentAgainstContrib: -53},
          {month: 'Apr', amt: 43000, percent: -2, percentAgainstContrib: -54},
          {month: 'May', amt: 51000, percent: 19, percentAgainstContrib: -46},
          {month: 'Jun', amt: 43000, percent: -16, percentAgainstContrib: -54},
          {month: 'Jul', amt: 49000, percent: 14, percentAgainstContrib: -48},
          {month: 'Aug', amt: 51000, percent: 4, percentAgainstContrib: -46},
          {month: 'Sept', amt: 54000, percent: 6, percentAgainstContrib: -43},
          {month: 'Oct', amt: 56000, percent: 4, percentAgainstContrib: -41},
          {month: 'Nov', amt: 44000, percent: -21, percentAgainstContrib: -53},  
          {month: 'Dec', amt: 38000, percent: -14, percentAgainstContrib: -60}      
        ]
      },
      {
        "year": 2018,
        "months": [
          {month: 'Jan', amt: 60000, percent: 58, percentAgainstContrib: -37},
          {month: 'Feb', amt: 63000, percent: 5, percentAgainstContrib: -33},
          {month: 'Mar', amt: 63000, percent: 0, percentAgainstContrib: -33},
          {month: 'Apr', amt: 53000, percent: -16, percentAgainstContrib: -44},
          {month: 'May', amt: 54000, percent: 2, percentAgainstContrib: -43},
          {month: 'Jun', amt: 53000, percent: -2, percentAgainstContrib: -44},
          {month: 'Jul', amt: 54000, percent: 2, percentAgainstContrib: -43},
          {month: 'Aug', amt: 57000, percent: 6, percentAgainstContrib: -40},
          {month: 'Sept', amt: 63000, percent: 11, percentAgainstContrib: -33},
          {month: 'Oct', amt: 68000, percent: 8, percentAgainstContrib: -28},
          {month: 'Nov', amt: 74000, percent: 9, percentAgainstContrib: -22},  
          {month: 'Dec', amt: 80000, percent: 8, percentAgainstContrib: -15}      
        ]
      }      
    ];
    return (
      <div className="App">
        <h2>Year-End Performance</h2>
        <LineChart width={600} height={300} data={yearEndData}>
          <Line type="monotone" dataKey="amt" stroke="#8884d8" />
          <CartesianGrid stroke="#ccc" />
          <XAxis dataKey="year" />
          <YAxis />
          <Tooltip />
        </LineChart>
        <h2>Monthly Performance (against previous month)</h2> 
        <form onSubmit={this.handleSubmit}>          
          <input className="btn btn-secondary" onClick={this.handleClick}  type="submit" value="2015" />
          <input className="btn btn-secondary" onClick={this.handleClick}  type="submit" value="2016" />
          <input className="btn btn-secondary" onClick={this.handleClick}  type="submit" value="2017" />
          <input className="btn btn-secondary" onClick={this.handleClick}  type="submit" value="2018" />
        </form>
        <LineChart width={600} height={300} data={monthlyData[this.state.activeYear].months}>
          <Line type="monotone" dataKey="percent" stroke="#8884d8" />
          <CartesianGrid stroke="#ccc" />
          <XAxis dataKey="month" />
          <YAxis />
          <Tooltip />
        </LineChart>
        <h2>Monthly Performance (against overall contribution)</h2> 
        <form onSubmit={this.handleSubmit}>          
          <input className="btn btn-secondary" onClick={this.handleClick}  type="submit" value="2015" />
          <input className="btn btn-secondary" onClick={this.handleClick}  type="submit" value="2016" />
          <input className="btn btn-secondary" onClick={this.handleClick}  type="submit" value="2017" />
          <input className="btn btn-secondary" onClick={this.handleClick}  type="submit" value="2018" />
        </form>
        <LineChart width={600} height={300} data={monthlyData[this.state.activeYear].months}>
          <Line type="monotone" dataKey="percentAgainstContrib" stroke="#8884d8" />
          <CartesianGrid stroke="#ccc" />
          <XAxis dataKey="month" />
          <YAxis />
          <Tooltip />
        </LineChart>
      </div>
    );
  }
}

export default App;



